import React, { useState, useEffect } from 'react'
import FormularioCadastro from './FormularioCadastro'
import fireDb from './firebase'

const Cadastro = () => {
    
    let [dTemp, setTemp] = useState('');

    useEffect(() => {
        fireDb.child('temperatura').on('value', dbPhoto => {
            if (dbPhoto.val() != null) {
                setTemp(dbPhoto.val())
            }
            else { setTemp('') }
        })
    }, [])

    const addEedit = obj => {


    }
    return (
        <div>
            <div className="jumbotron jumbotron-fluid">
                <div className="container">
                    <h1 className="display-4">Temperatura</h1>
                    <p className="lead">.</p>
                </div>
            </div>

            <div className="row">
                <div className="col-md-7">
                    <table className="table table-bordless table-stripped">
                        <thead className="thead-light">
                            <tr>
                                <td>Bloco</td>
                                <td>Sala</td>
                                <td>Status</td>
                                <td>Temperatura atual</td>
                                <td>Ações</td>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td>Aulas</td>
                                <td>24</td>
                                <td>Ligado</td>
                                <td>
                                    {dTemp}
                                </td>
                                <td> 
                                    <a className="btn btn-success" onClick={() => {fireDb.child('Dados').update({"estado":'ON'})}}>
                                        <i className="bi bi-toggle-on"></i>
                                    </a>

                                    <a className="btn btn-danger" onClick={() => {fireDb.child('Dados').update({"estado":0})}}>
                                        <i className="bi bi-toggle-off"></i>
                                    </a>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default Cadastro